import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Container,
  Paper,
  Typography,
  TextField,
  Button,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Checkbox,
  Grid,
  Divider,
  Slider,
  InputAdornment
} from '@mui/material';

const ComparablePropertiesSelection = () => {
  const [selectedComps, setSelectedComps] = useState([]);
  const [adjustmentFactors, setAdjustmentFactors] = useState({
    location: 1.0,
    size: 1.0,
    age: 1.0,
    condition: 1.0,
    amenities: 1.0
  });

  // Sample data - in a real app, this would come from an API
  const sampleComps = [
    { id: 1, address: '123 Main St', city: 'Denver', price: 1500000, sqft: 5000, yearBuilt: 2015, capRate: 5.2 },
    { id: 2, address: '456 Oak Ave', city: 'Denver', price: 1750000, sqft: 5500, yearBuilt: 2018, capRate: 4.9 },
    { id: 3, address: '789 Pine Blvd', city: 'Boulder', price: 2100000, sqft: 6200, yearBuilt: 2017, capRate: 4.7 },
    { id: 4, address: '101 Maple Dr', city: 'Golden', price: 1250000, sqft: 4800, yearBuilt: 2010, capRate: 5.5 },
    { id: 5, address: '202 Cedar Ln', city: 'Denver', price: 1650000, sqft: 5300, yearBuilt: 2016, capRate: 5.0 }
  ];

  const handleCompSelection = (compId) => {
    if (selectedComps.includes(compId)) {
      setSelectedComps(selectedComps.filter(id => id !== compId));
    } else {
      setSelectedComps([...selectedComps, compId]);
    }
  };

  const handleAdjustmentChange = (factor, value) => {
    setAdjustmentFactors({
      ...adjustmentFactors,
      [factor]: value
    });
  };

  const navigate = useNavigate();

  const handleContinue = () => {
    // Save the selected comps and adjustments
    const selectedCompData = sampleComps.filter(comp => selectedComps.includes(comp.id));
    console.log('Selected comps:', selectedCompData);
    console.log('Adjustment factors:', adjustmentFactors);
    
    // Navigate to the next step in the workflow using React Router
    navigate('/valuation-results');
  };

  return (
    <Container maxWidth="lg">
      <Paper elevation={3} sx={{ p: 3, my: 3 }}>
        <Typography variant="h4" gutterBottom>
          Select Comparable Properties
        </Typography>
        <Typography variant="body1" paragraph>
          Choose similar properties to use as comparables for valuation. Select at least 3 comparable properties 
          for the most accurate results.
        </Typography>

        <TableContainer component={Paper} sx={{ mb: 4 }}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Select</TableCell>
                <TableCell>Address</TableCell>
                <TableCell>City</TableCell>
                <TableCell>Price</TableCell>
                <TableCell>Size (sq ft)</TableCell>
                <TableCell>Year Built</TableCell>
                <TableCell>Cap Rate</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {sampleComps.map((comp) => (
                <TableRow key={comp.id} selected={selectedComps.includes(comp.id)}>
                  <TableCell padding="checkbox">
                    <Checkbox
                      checked={selectedComps.includes(comp.id)}
                      onChange={() => handleCompSelection(comp.id)}
                    />
                  </TableCell>
                  <TableCell>{comp.address}</TableCell>
                  <TableCell>{comp.city}</TableCell>
                  <TableCell>${comp.price.toLocaleString()}</TableCell>
                  <TableCell>{comp.sqft.toLocaleString()}</TableCell>
                  <TableCell>{comp.yearBuilt}</TableCell>
                  <TableCell>{comp.capRate}%</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>

        <Typography variant="h5" gutterBottom>
          Adjustment Factors
        </Typography>
        <Typography variant="body2" paragraph>
          Fine-tune the importance of different factors in your valuation.
        </Typography>

        <Grid container spacing={3} sx={{ mb: 4 }}>
          <Grid item xs={12} md={6}>
            <Typography gutterBottom>Location</Typography>
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              <Slider
                value={adjustmentFactors.location}
                min={0.5}
                max={1.5}
                step={0.05}
                onChange={(e, value) => handleAdjustmentChange('location', value)}
                sx={{ mr: 2, flex: 1 }}
              />
              <TextField 
                value={adjustmentFactors.location}
                size="small"
                InputProps={{
                  readOnly: true,
                  startAdornment: <InputAdornment position="start">×</InputAdornment>,
                }}
                sx={{ width: '80px' }}
              />
            </Box>
          </Grid>
          <Grid item xs={12} md={6}>
            <Typography gutterBottom>Size</Typography>
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              <Slider
                value={adjustmentFactors.size}
                min={0.5}
                max={1.5}
                step={0.05}
                onChange={(e, value) => handleAdjustmentChange('size', value)}
                sx={{ mr: 2, flex: 1 }}
              />
              <TextField 
                value={adjustmentFactors.size}
                size="small"
                InputProps={{
                  readOnly: true,
                  startAdornment: <InputAdornment position="start">×</InputAdornment>,
                }}
                sx={{ width: '80px' }}
              />
            </Box>
          </Grid>
          <Grid item xs={12} md={6}>
            <Typography gutterBottom>Age</Typography>
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              <Slider
                value={adjustmentFactors.age}
                min={0.5}
                max={1.5}
                step={0.05}
                onChange={(e, value) => handleAdjustmentChange('age', value)}
                sx={{ mr: 2, flex: 1 }}
              />
              <TextField 
                value={adjustmentFactors.age}
                size="small"
                InputProps={{
                  readOnly: true,
                  startAdornment: <InputAdornment position="start">×</InputAdornment>,
                }}
                sx={{ width: '80px' }}
              />
            </Box>
          </Grid>
          <Grid item xs={12} md={6}>
            <Typography gutterBottom>Condition</Typography>
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              <Slider
                value={adjustmentFactors.condition}
                min={0.5}
                max={1.5}
                step={0.05}
                onChange={(e, value) => handleAdjustmentChange('condition', value)}
                sx={{ mr: 2, flex: 1 }}
              />
              <TextField 
                value={adjustmentFactors.condition}
                size="small"
                InputProps={{
                  readOnly: true,
                  startAdornment: <InputAdornment position="start">×</InputAdornment>,
                }}
                sx={{ width: '80px' }}
              />
            </Box>
          </Grid>
          <Grid item xs={12} md={6}>
            <Typography gutterBottom>Amenities</Typography>
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              <Slider
                value={adjustmentFactors.amenities}
                min={0.5}
                max={1.5}
                step={0.05}
                onChange={(e, value) => handleAdjustmentChange('amenities', value)}
                sx={{ mr: 2, flex: 1 }}
              />
              <TextField 
                value={adjustmentFactors.amenities}
                size="small"
                InputProps={{
                  readOnly: true,
                  startAdornment: <InputAdornment position="start">×</InputAdornment>,
                }}
                sx={{ width: '80px' }}
              />
            </Box>
          </Grid>
        </Grid>

        <Divider sx={{ my: 3 }} />

        <Box sx={{ display: 'flex', justifyContent: 'flex-end' }}>
          <Button 
            variant="contained" 
            color="primary" 
            size="large"
            onClick={handleContinue}
            disabled={selectedComps.length < 1} // Ideally require at least 3 comps
          >
            Continue to Valuation
          </Button>
        </Box>
      </Paper>
    </Container>
  );
};

export default ComparablePropertiesSelection;
